import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Form, Switch, Modal, Button, Row, Col, Typography } from 'antd';
import BankDocs from './BankDocs';
import BankInfoStyleWrapper from './bankInfo.style';

function Assessment(props) {
  // Declare a new state variable, which we'll call "count"
  const [disabled] = useState(true);
  const [bankDetailsVisible, setBankDetailsVisible] = useState(false);
  const [accessment, setAssessment] = useState({});
  const onChange = (e, a) => {
    let newAccessment = { ...accessment, [a.currentTarget.id]: e };
    setAssessment(newAccessment);
  };
  return (
    <Form layout="inline" colon={false}>
      <Col xs={24} sm={20} md={20} lg={20} xl={20} xxl={8}>
        <Row>
          <Typography.Title level={4}>This Policy:</Typography.Title>
        </Row>
        <Col span={10}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Is this claim for a Pre-Existing condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'forPreExisting'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.forPreExisting
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Does it realte to a Pre-Exsting condition?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'relatePreExisting'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.relatePreExisting
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span>Do any other Policy Exclusions apply?</span>
            </Col>
            <Col span={8}>
              <Switch
                id={'otherExclusion'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.otherExclusion
                    : false
                }
              />
            </Col>
          </Row>
        </Col>
        <Col span={12} offset={2}>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Are all the charges reasonable and customary?
              </span>
            </Col>
            <Col span={8}>
              <Switch
                id={'reasonable'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.reasonable
                    : false
                }
              />
            </Col>
          </Row>
          <Row type="flex" justify="center" align="middle">
            <Col span={16}>
              <span style={{ display: 'block' }}>
                Does this claim adhere to the Policy Workding?
              </span>
            </Col>
            <Col span={8}>
              <Switch
                id={'adhere'}
                disabled={disabled}
                onChange={onChange}
                checked={
                  props.claim && props.claim.Acessment
                    ? props.claim.Acessment.adhere
                    : false
                }
              />
            </Col>
          </Row>
        </Col>
      </Col>
      <Col xs={24} sm={4} md={4} lg={4} xl={4} xxl={4}>
        <Row>
          <Typography.Title level={4}>Documents Bank Details:</Typography.Title>
        </Row>
        <Button type="primary" onClick={() => setBankDetailsVisible(true)}>
          FILE
        </Button>
        <BankInfoStyleWrapper>
          <Modal
            width={'70%'}
            visible={bankDetailsVisible}
            onOk={() => setBankDetailsVisible(false)}
            onCancel={() => setBankDetailsVisible(false)}
            footer={[
              <Button
                key={1}
                type="primary"
                onClick={() => setBankDetailsVisible(false)}
              >
                OK
              </Button>,
            ]}
          >
            <BankDocs key={1} claim={props.claim} />
          </Modal>
        </BankInfoStyleWrapper>
      </Col>
      <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={12}>
        <Col span={8}>
          <Row>
            <Typography.Title level={4}>Rationale:</Typography.Title>
          </Row>
          <span>
            {props.claim &&
            props.claim.Comments &&
            props.claim.Comments.sort((a, b) => {
              if (a.group === 'evaluate') return -1;
              return new Date(a.updatedAt) - new Date(b.updatedAt);
            })[0]
              ? props.claim.Comments.sort((a, b) => {
                  if (a.group === 'evaluate') return -1;
                  return new Date(a.updatedAt) - new Date(b.updatedAt);
                })[0].message
              : null}
          </span>
        </Col>
        <Col span={8}>
          <Row>
            <Typography.Title level={4}>Decision:</Typography.Title>
          </Row>
          <span>
            {props.claim &&
            props.claim.Comments &&
            props.claim.Comments.sort((a, b) => {
              if (a.group === 'decision') return -1;
              return new Date(a.updatedAt) - new Date(b.updatedAt);
            })[0]
              ? props.claim.Comments.sort((a, b) => {
                  if (a.group === 'decision') return -1;
                  return new Date(a.updatedAt) - new Date(b.updatedAt);
                })[0].message
              : null}
          </span>
        </Col>
        <Col span={6} offset={2}>
          <Row>
            <Typography.Title level={4}>Status:</Typography.Title>
          </Row>
          <span>{props.claim && props.claim ? props.claim.status : null}</span>
        </Col>
      </Col>
    </Form>
  );
}

export default connect(
  state => ({ idToken: state.Auth.idToken }),
  {}
)(Form.create()(Assessment));
